export default {
  props: {
	  start:{
		  type:Number
	  }
  },
  data () {
    return {
      years: [],
      months: [],
      days: [],
	  hours:[],
	  minutes: [],
      dayLength: 30,
	  hourLength: 24,
	  minuteLength: 60,
      value: [0, 0, 0],
      visible: false,
	  flag:false,
      indicatorStyle: `height: ${Math.round(uni.getSystemInfoSync().screenWidth / (750 / 100))}rpx;`
    }
  },
  mounted () {
    const time = new Date()
    const year = time.getFullYear()
    const month = time.getMonth() + 1
	const hour = time.getHours()
	const minute = time.getMinutes()
    this.dayLength = this.getDaysInOneMonth(year, month)
	// 这里自定义传参，来设置开始时间和结束时间，不再演示
    this.getYears()
    this.getMonth()
    this.getDays()
	this.getHours()
	this.getMinute()
  },
  methods: {
    bindChange: function (e) {
	  this.flag = true
      const val = e.detail.value
      // this.year = this.years[val[0]] 
      this.month = this.months[val[0]]
      this.day = this.days[val[1]]
	  this.hour = this.hours[val[2]]
	  this.minute = this.minutes[val[3]]
    },
    // 获得年份
    getYears () {
      const time = new Date()
      for (let i = time.getFullYear(); i < 2085; i++) {
        this.years.push(i)
      }
    },
    // 获取月份
    getMonth () {
      const time = new Date()
      for (let i = time.getMonth() + 1; i <= 12; i++) {
        this.months.push(i)
      }
    },
    getDays () {
      //  this.dayLength
      const time = new Date()
      for (let i = time.getDate(); i <= this.dayLength; i++) {
        this.days.push(i)
      }
    },
	getHours () {
		const time = new Date()
		for(let i = time.getHours(); i <= this.hourLength; i++) {
			this.hours.push(i);
		}
	},
	getMinute () {
		const time = new Date()
		for(let i = time.getMinutes(); i <= this.minuteLength; i++) {
			this.minutes.push(i);
		}
	},
    // 获取某年某月多少天
    getDaysInOneMonth (year, month) {
      month = parseInt(month, 10)
      var d = new Date(year, month, 0)
      return d.getDate()
    },
    // 取消
    cacel () {
      this.$emit('cancel', false)
    },
    // 确认
    sure () {
		if(!this.flag){
			   this.$emit('cancel', false)
			this.$emit('sure', { year: this.years[0], month: this.months[0], day: this.days[0], hour:this.hours[0], minute:this.minutes[0]})
			return
		}
      this.$emit('cancel', false)
      this.$emit('sure', { year: this.year, month: this.month, day: this.day, hour:this.hour, minute:this.minute})
    }
  }
}
